/* eslint-disable */
import io from "socket.io-client";
import { useRecoilState, useRecoilValue } from "recoil";
import {
  usernameState,
  publicRoomState,
  userState,
  setShowChatState,
  userWantsToJoinState,
  windowWidthState,
} from "../atoms";

const socket = io("http://localhost:3001");

socket.on("connect", () => {
  console.log("connected to server");
});
const TryDemo = (props) => {
  const [user, setUser] = useRecoilState(userState);
  const [username, setUsername] = useRecoilState(usernameState);
  const [publicRoom, setPublicRoom] = useRecoilState(publicRoomState);
  const [showChat, setShowChat] = useRecoilState(setShowChatState);
  const [userWantsToJoin, setUserWantsToJoin] =
    useRecoilState(userWantsToJoinState);
  const windowWidth = useRecoilValue(windowWidthState);

  const joinRoom = (room, usernameA, color) => {
    if (username !== "" && room !== "") {
      // socket.emit("get_rooms");
      // console.log("zzkljfksdjfssfdlfas");
      room === "public"
        ? socket.emit("join_public_room", [room, usernameA, color])
        : socket.emit("join_private_room", [room, usernameA, color]);
    }
  };

  const handleNewUserForm = async (e) => {
    e.preventDefault();
    const badWords = ["fuck", "penis", "bitch", "pussy", "fag"];
    const bgColors = [
      "#FFDAB9",
      "#89CFF0",
      "#A0DB8E",
      "#FEC9C1",
      "#F1E694",
      "#DAA8FF",
      "#97C1D9",
      "#F7CE8A",
      "#E5B1AA",
      "#88D4AB",
    ];

    let containsBadWord = false;
    badWords.forEach((word) =>
      username.includes(word) ? (containsBadWord = true) : null
    );
    if (containsBadWord) {
      alert("Please try to use appropriate names");
      return "";
    }
    if (username !== "" && username.match(/[0-9]/g)) {
      let isPublic;
      let minutes = new Date(Date.now()).getMinutes();
      minutes = minutes < 10 ? "0" + minutes : minutes;
      if (publicRoom !== null) isPublic = publicRoom ? "public" : "private";
      const newUser = {
        username,
        room: isPublic,
      };

      setUser(newUser);
      setShowChat(true);
      joinRoom(isPublic, username, bgColors[Math.floor(Math.random() * 10)]);
      setUserWantsToJoin(true);
      if (publicRoom) {
        const messageData = {
          room: isPublic,
          author: "Draft Bot",
          message: `Welcome, ${username.toUpperCase()}! Commands are on the left and more details can be found in the documentation. `,
          time: new Date(Date.now()).getHours() + ":" + minutes,
        };
        socket.emit("send_message", messageData);
      }
    } else {
      alert("Please enter a valid username \nMust include at least 1 number");
    }
  };

  return (
    <div
      className={`flex flex-col justify-center items-center min-w-[${
        windowWidth - 60
      }px] h-[200px] rounded-md`}
    >
      <form
        className="font-opensans w-[100%] text-gray-200 flex flex-col justify-center items-center  mt-0"
        onSubmit={handleNewUserForm}
      >
        <p className="">Choose a username</p>
        <p className="text-[13px] mb-4">
          {"(Please include at least 1 number)"}
        </p>
        <input
          type="text"
          placeholder="username"
          className="text-black rounded-lg text-sm px-1 py-1"
          onChange={(e) => setUsername(e.target.value)}
        />

        <div className="flex justify-center items-center mt-6">
          <button className=" text-white text-sm  bg-[#6482d0] hover:bg-[#415da3] duration-300 rounded-2xl w-fit py-1 px-[22px]">
            Sign-in
          </button>
        </div>
      </form>
    </div>
  );
};

export default TryDemo;
